@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;400&family=Roboto:wght@100;300;400;500;700&display=swap);
* {
  padding: 0;
  margin: 0;
  text-decoration: none;
  list-style: none;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
}

body::-webkit-scrollbar {
  width: 10px;
  height: 15px;
}
body::-webkit-scrollbar-track {
  background-color: #590d22;
}
body::-webkit-scrollbar-thumb {
  background-image: linear-gradient(45deg, orange, gold);
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.12) 0 3px 13px 1px;
}

html {
  font-size: 62.5%;
}
body {
  font-size: 1.6rem;
}
.nav__item {
  list-style: none;
  font-size: 2rem;
}

.nav__link {
  transition: all 500ms ease;
}
.nav__link:hover {
  color: gold;
  cursor: pointer;
  transition: all 500ms ease;
}
a {
  text-decoration: none;
  color: wheat;
}

.nav {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 14vh;
  background: #590d22;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}
.nav__brand {
  text-transform: uppercase;
}

.nav__menu {
  display: flex;
  align-items: center;
  justify-content: space-around;
  grid-gap: 3rem;
  gap: 3rem;
}
.nav__toggler {
  display: none;
}
.nav__toggler div {
  width: 2.5rem;
  height: 0.2rem;
  margin: 0.4rem;
  background: rgb(204, 204, 204);
  transition: 0.3s ease-in;
}

@media screen and (max-width: 768px) {
  .nav__toggler {
    display: block;
    cursor: pointer;
  }
  .nav__menu {
    position: fixed;
    top: 7vh;
    right: 0;
    height: 93vh;
    width: 50vw;
    background: #590d22;
    flex-direction: column;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    transition: 0.5s ease-in;
    box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
  }
}

.nav__active {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

.toggle .line1 {
  -webkit-transform: rotate(-45deg) translate(-4px, 5px);
          transform: rotate(-45deg) translate(-4px, 5px);
}
.toggle .line2 {
  opacity: 0;
}
.toggle .line3 {
  -webkit-transform: rotate(45deg) translate(-4px, -5px);
          transform: rotate(45deg) translate(-4px, -5px);
}

.avatar__container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 26vw;
}

.avatar {
  width: 8.5rem;
  margin-top: 2px;
  margin-right: 10px;
  border-radius: 50%;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.avatar__name {
  color: wheat;
  font-size: 2rem;
}

@media screen and (max-width: 420px) {
  .avatar__name {
    font-size: smaller;
  }
  .avatar {
    width: 7rem;
  }
  .nav__item {
    list-style: none;
    font-size: smaller;
  }
}

.home {
  width: 100%;
  height: 86vh;
  background-image: url('https://images.unsplash.com/photo-1571171637578-41bc2dd41cd2?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=870&q=80');
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin: 0;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.aboutme {
  height: 100vh;
  background-color: #a4133c;
  color: wheat;
}

.aboutme__title {
  font-size: 2.5rem;
  font-weight: bold;
  text-align: center;
  padding: 5rem 0 0 0;
  letter-spacing: 1px;
}

.aboutme__info {
  display: flex;
  margin: 3rem;
}

.aboutme__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  line-height: 3rem;
  height: 80vh;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  border-radius: 9px 0 0 9px;
  background-color: #590d22;
}

.story__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 6rem;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  background-color: #590d22;
  border-radius: 0 9px 9px 0;
  height: 80vh;
  letter-spacing: 1.5px;
  line-height: 3rem;
  text-align: center;
}

.story__container__title {
  font-size: 2.3rem;
  font-weight: bold;
  text-align: center;
  padding: 2rem;
  letter-spacing: 1.5px;
}

@media screen and (max-width: 768px) {
  .story__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding: 1rem;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
      rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
      rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    background-color: #590d22;
    border-radius: 0 0 9px 9px;
    height: 70vh;
    letter-spacing: 1.5px;
    line-height: 3rem;
    text-align: center;
    overflow: auto;
    padding: 0 5rem;
  }
  .aboutme {
    height: auto;
    background-color: #a4133c;
    color: wheat;
    padding-bottom: 3rem;
  }
  .aboutme__info {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    letter-spacing: 1.5px;
    line-height: 3rem;
    text-align: center;
    margin-top: 3rem;
  }
  .aboutme__list {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem;
    line-height: 3rem;
    height: auto;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
      rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
      rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    border-radius: 9px 9px 0 0;
    background-color: #590d22;
    width: 100%;
  }

  .story__container__title {
    padding-bottom: 3rem;
  }
}

.projects {
  height: auto;
  background-color: #800f2f;
  color: wheat;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
}

.projects-title {
  padding-top: 5rem;
  padding-bottom: 2rem;
  font-size: 2.5rem;
  letter-spacing: 2px;
  text-align: center;
}

.card {
  border-radius: 2px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.5), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 1rem;
  width: 100%;
  align-items: center;
  overflow: auto;
}

.card__image {
  width: 90rem;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
  border-radius: 7px;
  object-fit: cover;
  margin-top: 2rem;
}
.card__image2 {
  width: 34.5rem;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
  border-radius: 7px;
  object-fit: cover;
  margin-top: 2rem;
  margin-left: 2rem;
}

.card__image__container {
  display: flex;
}

.card__title {
  font-size: 2rem;
  font-weight: bold;

  text-align: center;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.card__description {
  font-size: 1.5rem;

  text-align: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  letter-spacing: 1px;
}

.card__url {
  background-color: orange;
  color: #780116;
  border: none;
  padding: 1.5rem;
  border-radius: 10px;
  font-size: 2rem;
  letter-spacing: 3px;
  transition: all 500ms ease;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
  margin-top: 2rem;
  margin-bottom: 5rem;
}

.card__url:hover {
  background-color: gold;
  cursor: pointer;
  transition: all 500ms ease;
}

@media screen and (max-width: 950px) {
  .card__image {
    width: 50rem;
  }
  .card__image2 {
    width: 22rem;
  }
}

@media screen and (max-width: 768px) {
  .card__image {
    width: 45rem;
  }
  .card__image2 {
    width: 17rem;
  }
}

@media screen and (max-width: 650px) {
  .card__image {
    width: 100%;
  }
  .card__image2 {
    width: 100%;
    margin-right: 1rem;
  }
  .card__image__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.skills {
  background-color: #590d22;

  color: wheat;
  height: 100vh;
}

.skillset__title {
  font-size: 2.5rem;
  font-weight: bold;
  text-align: center;
  padding-top: 5rem;
  padding-bottom: 2rem;
  letter-spacing: 1px;
}

.skills__container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 1rem;
  padding: 2rem;
  width: 100%;
  height: 80vh;
  align-items: center;
  overflow: auto;
}

.skills__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 5rem;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  background-color: #800f2f;
  border-radius: 9px;
  letter-spacing: 1.5px;
  line-height: 3rem;
  text-align: center;
}

.skills__img {
  width: 10rem;
  object-fit: cover;
  border-radius: 5px;
}

.resume {
  height: auto;
}

.title-container {
  background-image: url(/portfolio/static/media/resumebg.50a729cb.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.name-container {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  height: 20vh;
  padding-left: 1rem;
  background-color: rgba(0, 0, 0, 0.6);
}
.name {
  color: #fff;
}

.row {
  display: flex;
}

.columnleft {
  flex: 40% 1;
  padding: 2rem;
  background-color: #590d22;
  color: wheat;
}

.columnright {
  flex: 60% 1;
  background-color: rgba(128, 15, 47, 1);
  padding: 2rem;
  color: wheat;
}

.courses > ul {
  list-style-type: none;
}

.courses > li::before {
  content: '\2022';

  font-weight: bold;
  display: inline-block;
  width: 1em;
  margin-left: -1em;
}

.courses > li {
  justify-content: flex-start;
  font-size: smaller;
}

.inst {
  margin-top: 1rem;
}

.resume-titles {
  margin-top: 3rem;
}

.captionthis {
  color: #fff;
  font-size: 2rem;
  font-weight: bold;
}

.skill-element {
  font-weight: 700;
  letter-spacing: 1px;
  font-style: italic;
}

.contact-container {
  height: 30vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #780116;

  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
}

.contact-btn {
  background-color: orange;
  color: #780116;
  border: none;
  padding: 3rem;
  border-radius: 10px;
  font-size: 3rem;
  letter-spacing: 3px;
  transition: all 500ms ease;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
}

.contactme {
  color: #780116;
}

.contact-btn:hover {
  background-color: gold;
  cursor: pointer;
  transition: all 500ms ease;
}

